import Vue from 'vue'
import Router from 'vue-router'
import Root from '../components/common/root/root'
import ProductDetail from '../components/home/productDetail/productDetail'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Root',
      component: Root
    },
    {
      path: '/productdetail',
      name: 'ProductDetail',
      component: ProductDetail
    }
  ]
})



